import { useParams } from 'react-router-dom'

const User = (users) => {
  const id = useParams().id
  const user = users.users.find((u) => u.id === id)
  if (!user) {
    return null
  }

  return (
    <div style={{ marginTop: 30 }}>
      <h2>{user.name}</h2>
      <h3 style={{ marginTop: 20 }}>added blogs:</h3>
      <ul>
        {user.blogs.map((blog) => (
          <li key={blog.id}>{blog.title}</li>
        ))}
      </ul>
    </div>
  )
}

export default User
